* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.state {
  max-width: 640px;
  width: calc(100% - 100px);
  margin: 0 auto;
  border: 2px solid #333;
  padding: 30px;
}
.state h1 {
  font-size: 20px;
  margin: 20px 0;
}
.state input {
  border: 0;
  border-bottom: 1px solid #333;
  border-radius: 0;
  margin-bottom: 10px;
}
.state button {
  border-radius: 0;
  padding: 2px 10px;
  margin-left: 20px;
  font-size: 14px;
  border: 0;
  background-color: teal;
  color: #fff;
}
.state button:hover {
  background-color: #333;
  color: #fff;
}
.state p {
  font-size: 12px;
}